<template>
	<div class="body">
		<div class="search_group">
			<div class="search_group_top">
				<div class="search_box">
					<el-select v-model="mother_fund_id" filterable clearable placeholder="自管基金全称">
						<el-option v-for="item in mother_fund_list" :key="item.id" :label="item.name" :value="item.id">
						</el-option>
					</el-select>
				</div>
			</div>
			<div class="search_group_bottom flex_start">
				<div class="search_box">
					<el-cascader :options="investerType" v-model="invester_type" @change="chooseInvestorType" :show-all-levels="false" clearable placeholder="投资人类别">
  					</el-cascader>
				</div>
				<div class="search_box">
					<el-select v-model="investor_id" filterable clearable placeholder="请选择投资人">
						<el-option v-for="item in all_company_list" :key="item.id" :label="item.name" :value="item.id">
						</el-option>
					</el-select>
				</div>
				
				<div class="search_box">
					<el-select v-model="ir_id" filterable clearable placeholder="IR经理">
						<el-option v-for="item in allUserList" :key="item.id" :label="item.name" :value="item.id">
						</el-option>
					</el-select>
				</div>
				<div class="search_box">
					<el-select v-model="manager_id" filterable clearable placeholder="客户经理">
						<el-option v-for="item in allUserList" :key="item.id" :label="item.name" :value="item.id">
						</el-option>
					</el-select>
				</div>
				<div class="btn_box">
					<button @click="getInitData(1)">搜索</button>
				</div>
			</div>
		</div>
		<div class="table_title flex_between">
			<div class="">
				自管基金投资人总数<span>(&nbsp;{{ total }}&nbsp;)</span>
			</div>
			<div class="flex_end">
				<button class="blue_btn" @click="downloadExcel">
					<span class="iconfont icon-daoru"></span>
					导出excel表格
				</button>
			</div>
		</div>
		<div class="table">
			<el-table :data="tableData" border style="width: 100%">
				<el-table-column sortable prop="id" label="序号" width="80">
					<template slot-scope="scope">
						{{ (scope.$index + 1) + (currentPage - 1) * pageSize }}
					</template>
				</el-table-column>
				<el-table-column sortable prop="petfundname" label="自管基金全称" width="200">
				</el-table-column>
				<el-table-column sortable prop="id" label="ID" width="80">
				</el-table-column>
				
				<el-table-column sortable prop="number" label="编号" width="80">
				</el-table-column>
				<el-table-column sortable prop="investor_class" label="投资人类别" width="130">
				</el-table-column>
				<el-table-column sortable prop="is_gp" label="是否为GP" width="130">
					<template slot-scope="scope">
						{{ scope.row.is_gp?'是':'否' }}
					</template>
				</el-table-column>
				<el-table-column sortable prop="investor_name" label="投资人名称" width="150">
				</el-table-column>
				<el-table-column sortable prop="ir_name" label="IR经理" width="130">
				</el-table-column>
				<el-table-column sortable prop="manager_name" label="客户经理" width="130">
				</el-table-column>
				<el-table-column sortable prop="invest_date" label="入伙时间" width="130">
				</el-table-column>
				<el-table-column sortable prop="subscribe" label="认缴（万元）" width="150">
				</el-table-column>
				<el-table-column sortable prop="amount_paid_in" label="实缴（万元）" width="150">
				</el-table-column>
				<el-table-column sortable prop="amount_paid_in_plan" label="实缴进度" width="150">
					<template slot-scope="scope">
						{{ scope.row.amount_paid_in_plan }}&nbsp;%
					</template>
				</el-table-column>
				<el-table-column sortable prop="back" label="回款返本（万元）" width="180">
				</el-table-column>
				<el-table-column sortable prop="income" label="回款收益-税后（万元）" width="200">
				</el-table-column>
				<el-table-column sortable prop="service_fee" label="应收服务费（万元）" width="180">
				</el-table-column>
				<el-table-column sortable prop="is_change" label="工商状态" width="150">
					<template slot-scope="scope">
						{{ scope.row.is_change?'待变更':'正常' }}
					</template>
				</el-table-column>
				<el-table-column sortable prop="current_value" label="现值（万元）" width="140">
				</el-table-column>
				<el-table-column sortable prop="unit" label="币种" width="120">
					<template slot-scope="scope">
						{{ scope.row.unit == 1?'人民币':'美元' }}
					</template>
				</el-table-column>
				<el-table-column sortable prop="interest" label="计算中（万元）" width="150">
				</el-table-column>
				<el-table-column sortable prop="is_check" label="已确认（万元）" width="150">
				</el-table-column>
				<el-table-column sortable prop="is_freeze" label="是否冻结" width="150">
					<template slot-scope="scope">
						{{ scope.row.is_freeze == 1?'是':'否' }}
					</template>
				</el-table-column>
				<template slot="empty">
					<div class="table-nodata flex_start">
						<img src="../../images/table_nodata.png">
						<span>没有对应数据</span> 
					</div>
				</template>
			</el-table>
			<el-pagination v-if="total" @size-change="everyPageSizeChange" prev-text="上一页" next-text="下一页" background @current-change="changeCurrentPage" :current-page="currentPage" :page-size="pageSize" layout="sizes, prev, pager, next, jumper" :total="total">
			</el-pagination>
		</div>
	</div>
</template>

<script>
	import { dialogConfirm, dialogConfirmHasbtn } from '../../config/mUtils'
	import { getUserlistApi,investorListReportSelfApi,getInvestorInvestGroupFundSelectListApi,investorDownloadReportSelfApi } from '../../config/api'
	
	export default {
		name: 'motherFundInvesterTotal',
		data() {
			return {
				company_id: '',
				mother_fund_id: '',
				investor_id: '',
				ir_id: '',
				manager_id: '',
				invester_type: [],
				currentPage: 1,
				pageSize: 10,
				total: 0,
				
				allUserList: [],
				mother_fund_list: [],
				all_company_list: [],
				// 投资人类别
				investerType: [
					{
						value: 0,
						label: '基金',
						children: [
							{
								value: 3,
								label: '母基金'
							},
							{
								value: 5,
								label: '自管基金'
							},
							{
								value: 4,
								label: '子基金'
							}
						]
					},
					{
						value: 1,
						label: '管理公司'
					},
					{
						value: 2,
						label: '一般投资人'
					}
				],
				tableData: [ ]
			}
		},
		created() {
			this.getAllUserList()
			this.getFundSelectList(3,1)
			this.getInitData()
		},
		mounted() {},
		components: {},
		methods: {
			getInitData(is_init_page){
				if (is_init_page) {
					this.currentPage = 1;
				}
				var type = '';
				if (this.invester_type.length) {
					type = this.invester_type[this.invester_type.length - 1];
				}
				
				investorListReportSelfApi({
					pet_fund_id: this.mother_fund_id,
					type: type,
					
					investor_id: this.investor_id,
					ir_id: this.ir_id,
					manager_id: this.manager_id,
					page: this.currentPage,
					pageSize: this.pageSize
				}).then(res => {
					if(res.data.msgNo == 1) {
						this.tableData = res.data.payload.data;
						this.total = res.data.payload.total;
					} else {
						dialogConfirm("info", res.data.message);
					}
				})
			},
			downloadExcel(){
				var type = '';
				if (this.invester_type.length) {
					type = this.invester_type[this.invester_type.length - 1];
				}
				
				investorDownloadReportSelfApi({
					pet_fund_id: this.mother_fund_id,
					investor_id: this.investor_id,
					ir_id: this.ir_id,
					manager_id: this.manager_id,
					type: type,
				}).then(res => {
					//console.log(res);
					if(res.data.msgNo == 1) {
						window.location.href = res.data.payload.path;
					} else {
						dialogConfirm("info", res.data.message);
					}
				})
			},
			getFundSelectList(e,is_fund_list){
				getInvestorInvestGroupFundSelectListApi({
					type: e
				}).then(res => {
					//console.log(res);
					if(res.data.msgNo == 1) {
						if (e == 3) {
							if (is_fund_list) {
								this.mother_fund_list = res.data.payload;
							} else{
								this.all_company_list = res.data.payload;
							}
						} else{
							this.all_company_list = res.data.payload;
						}
					} else {
						dialogConfirm("info", res.data.message);
					}
				})
			},
			// 获取所有人员列表
			getAllUserList(){
				getUserlistApi({
					number: 10000,
					page: 1
				}).then(res => {
					//console.log(res);
					if(res.data.msgNo == 1) {
						this.allUserList = res.data.payload.data;
					} else {
						dialogConfirm("info", res.data.message);
					}
				})
			},
			chooseInvestorType(e){
				this.investor_id = '';
				if(!e.length){
					this.all_company_list = [];
					return
				}
				var data = e[e.length - 1];
				var type = '';
				if (data == 1) {
					type = 1;
				} else if(data == 2) {
					type = 6
				} else if(data == 3) {
					type = 2
				} else if(data == 4) {
					type = 4
				} else {
					type = 3
				}
				this.getFundSelectList(type)
			},
			
			
			
			// 分页功能模块
			changeCurrentPage(e){
				this.currentPage = e;
				this.getInitData()
			},
			everyPageSizeChange(e){
				this.pageSize = e;
				this.getInitData()
			},
		},
	}
</script>

<style scoped="scoped">
	.body{
		padding: 0 !important;
	}
	.search_group{
		padding: 30px 0;
    		border-bottom: 1px solid #F1F1F1;
	}
	.search_group_bottom{
		margin-top: 20px;
	}
	.search_group_bottom .search_box{
		width: 140px;
	}
	
	.search_box{
		width: 170px;
		height: 30px;
		margin-right: 13px;
	}
	.btn_box{
		margin-left: 24px;
	}
	.btn_box button{
		width: 80px;
		height: 30px;
		color: white;
		font-size: 14px;
		border-radius: 20px;
		background: #6292F4;
	}
	.table_title{
		margin: 26px 0;
		color: #6292F4;
		font-size: 14px;
	}
	.table_title span{
		color: #6292F4;
	}
</style>


